import { Component, OnInit } from '@angular/core';
import { Product } from '../../../shared/classes/product';
import { ProductService } from '../../../shared/services/product.service';

@Component({
  selector: 'app-product-tabs',
  templateUrl: './product-tabs.component.html',
  styleUrls: ['./product-tabs.component.scss']
})
export class ProductTabsComponent implements OnInit {

  public newProducts: Product[] = [];
  public bestSellerProducts: Product[] = [];

  constructor(public productService: ProductService) {
      this.productService.getProducts.subscribe(response => {
          this.newProducts = response.filter(item =>
              item.new).sort((a, b) => { // i made a filtered sorted array for new products
              return a.id > b.id ? -1 : 1;
          });
      });

      this.productService.getProducts.subscribe(response => {
          this.bestSellerProducts = response.filter(item =>
              item.collections.some(prod =>
                  prod.indexOf('best sellers') > -1)) // get the product with specific collection
              .sort((a, b) => { // i made a filtered sorted array for specific collection products
              return a.id > b.id ? -1 : 1;
          });
      });
  }

  ngOnInit(): void {
  }

}
